<template>
	<div id="search">
		<search-header v-on:change="getSearchKey">
			<span class="search-btn" slot="icon" @click="searchClick">搜素</span>
		</search-header>
		
		<div class="search-container">
			<div class="recent-search">
				<div class="title">
					<span>最近搜索</span>
					<img class="del-icon" src="../../assets/common_btn_del@2x.png" @click="delClick"/>
				</div>
				<div class="recent-list list-wrap">
					<ul class="clearfix">
						<li v-for="(item,index) in historyArr" @click="searchKeyClick(item)">{{item}}</li>
					</ul>
				</div>
			</div>
			
			<div class="hot-search">
				<div class="title">
					<span>热搜</span>
				</div>
				<div class="hot-list list-wrap">
					<ul class="clearfix">
						<li v-for="(item,index) in hotsearch" @click="searchKeyClick(item.value)">{{item.value}}</li>
					</ul>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import SearchHeader from '@/components/Header/SearchHeader'
	
export default {
	data(){
		return {
			searchVal:'',
			historyArr:[],
			hotsearch:[],	
		}
	},
  components: {
  	SearchHeader
  },
  created(){
  	let searchVal = localStorage.getItem('searchVal')
  	if(searchVal){
  		this.historyArr = JSON.parse(searchVal)
  	}
  	this.gethotsearch()
  },
  methods:{
  	getSearchKey(val){
  		this.searchVal = val
  	},
  	//搜索按钮
  	searchClick(){
  		this.historyArr.push(this.searchVal)
  		let newSet = new Set(this.historyArr)
  		this.historyArr = Array.from(newSet)
  		localStorage.setItem('searchVal',JSON.stringify(this.historyArr))
  		let storageArr = localStorage.getItem('searchVal')
  		this.toGoodsList(this.searchVal)
  	},
  	delClick(){
  		this.$popup({
  			title:'确认删除全部历史记录?',
  			content:'',
  		}).then((res)=>{
  			console.log(res)
  			if(res == 'sure'){
  				this.historyArr = []
  				localStorage.removeItem('searchVal')
  			}
  		})
  	},
  	gethotsearch(){
  		let key = 'sys.other.hotsearch'
  		this.$Api.getReferenceItemList(key).then((res)=>{
  			console.log(res)
  			if(res.q.s == 0){
  				this.hotsearch = res.q.items
  			}
  		})
  	},
  	
  	//点击搜素历史
  	searchKeyClick(searchVal){
  		this.toGoodsList(searchVal)
  	},
  	
  	toGoodsList(searchVal){
  		let params = {
  			a:1,
  			type:1,
  			searchKey:searchVal
			}
			if(this.$route.query.id==1){
				params.type = 2
			}else{
				params.type = 1
			}
  		this.$router.push({name:'GoodsList',query:params})
  	},
  	
  },
}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#search{
		.search-btn{
			display: inline-block;
			font-size: 0.4rem;
			border-radius: 0.4rem;
			width: 1.33rem;
			height: 0.8rem;
			line-height: 0.8rem;
			text-align: center;
			background: #fff;
			color: #de7fef;
			margin-left: 0.27rem;
		}
		.search-container{
			padding: 0.4rem 0;
			.recent-search,
			.hot-search{
			}
			.title{
				font-size: 0.4rem;
				color: #333;
				padding:0 0.4rem; 

				.del-icon{
					float: right;
					width: 0.59rem;
					height: 0.59rem;
				}
			}
			.list-wrap{
				ul{
					margin: 0.4rem 0 0.67rem;
					padding-left: 0.4rem; 
					>li{
						float: left;
						font-size: 0.35rem;
						height: 0.8rem;
						line-height: 0.8rem;
						min-width: 1.6rem;
						max-width:3.2rem;
						overflow: hidden;
						border-radius: 0.4rem;
						padding: 0 0.4rem;
						margin-right: 0.27rem;
						margin-bottom: 0.27rem;
						text-align: center;
						color: #333;
						background: #f4f4f4;
						@include textflow-one;
					}
				}
			}
		}
	}
</style>


